<template>
  <v-container>
    <v-layout row>
      <v-flex xs12>
        <h2 class="primary--text">Login</h2>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-card-text>
            <v-container>
              <form @submit.prevent="onSignin">
                <v-layout row>
                  <v-flex xs12>
                    <v-text-field
                      name="email"
                      label="Mail"
                      id="email"
                      v-model="email"
                      type="email"
                      required>
                    </v-text-field>
                  </v-flex>
                </v-layout>

                <v-layout row>
                  <v-flex xs12>
                    <v-text-field
                      name="password"
                      label="Password"
                      id="password"
                      v-model="password"
                      type="password"
                      required>
                    </v-text-field>
                  </v-flex>
                </v-layout>
                <v-layout row>
                  <v-flex xs12>
                    <v-btn type="submit">
                      Sign In
                    </v-btn>
                  </v-flex>
                </v-layout>
              </form>
            </v-container>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

  export default {
    data() {
      return {
        email: '',
        password: '',
      }
    },
    computed: {
      ...mapGetters([
        'getUser'
      ]),
    },
    watch: {
      getUser(value) {
        if( value !== null && value !== undefined) {
          this.$router.push('/');
        }
      }
    },
    methods: {
      ...mapActions([
        'signUserInAction'
      ]),
      onSignin() {
        //vuex
        // console.log("signup: ",{ email: this.email, password: this.password, confpassword: this.confpassword})
        const ob = {
          email: this.email,
          password: this.password
        }
        this.signUserInAction(ob);
      }
    }
  }
</script>



